(function() {
  angular.module('mnoEnterpriseAngular').factory('DashboardUser', ["$http", "$q", function($http, $q) {
    var service;
    service = {};
    service.update = function(data) {
      return $http.put("/jpi/v1/current_user", {
        user: data
      });
    };
    service.updatePassword = function(newPassword, confirmPassword, currentPassword) {
      return $http.put("/auth/users/update_password", {
        user: {
          password: newPassword,
          password_confirmation: confirmPassword,
          current_password: currentPassword
        }
      });
    };
    service.deletionRequest = function() {
      return $http.post("/mnoe/jpi/v1/deletion_requests");
    };
    service.cancelDeletionRequest = function(token) {
      return $http["delete"]("/mnoe/jpi/v1/deletion_requests/" + token);
    };
    service.resendDeletionRequest = function(token) {
      return $http.put("/mnoe/jpi/v1/deletion_requests/" + token + "/resend");
    };
    return service;
  }]);

}).call(this);



(function() {
  angular.module('mnoEnterpriseAngular').factory('DhbTeamSvc', ["$http", "$window", function($http, $window) {
    var service;
    service = {};
    service.routes = {
      basePath: function() {
        return "/mnoe/jpi/v1/organizations/" + service.config.id + "/teams";
      },
      loadPath: function() {
        return "" + (service.routes.basePath());
      },
      createPath: function() {
        return "" + (service.routes.basePath());
      },
      showPath: function(id) {
        return "/mnoe/jpi/v1/teams/" + id;
      },
      updatePath: function(id) {
        return service.routes.showPath(id);
      },
      addMembersPath: function(id) {
        return (service.routes.showPath(id)) + "/add_users";
      },
      removeMembersPath: function(id) {
        return (service.routes.showPath(id)) + "/remove_users";
      },
      destroyPath: function(id) {
        return service.routes.showPath(id);
      }
    };
    service.defaultConfig = {
      delay: 1000 * 60 * 2
    };
    service.config = {
      id: null,
      timerId: null,
      $q: null
    };
    service.data = [];
    service.getId = function() {
      return service.config.id;
    };
    service.configure = function(opts) {
      angular.copy(opts, service.config);
      return angular.extend(service.config, service.defaultConfig);
    };
    service.load = function(force) {
      var self;
      if (force == null) {
        force = false;
      }
      self = service;
      if ((self.config.$q == null) || force) {
        self.config.$q = $http.get(self.routes.loadPath()).then(function(success) {
          return angular.copy(success.data.teams, self.data);
        });
      }
      return self.config.$q;
    };
    service.startAutoRefresh = function() {
      var self;
      self = service;
      if (self.config.timerId == null) {
        return self.config.timerId = $window.setInterval((function() {
          return self.load(true);
        }), self.config.delay);
      }
    };
    service.stopAutoRefresh = function() {
      var self;
      self = service;
      if (self.config.timerId != null) {
        $window.clearInterval(self.config.timerId);
        return self.config.timerId = null;
      }
    };
    service.reload = function() {
      self.stopAutoRefresh();
      return self.load(true).then(self.startAutoRefresh);
    };
    service.setup = function(configOpts) {
      var self;
      if (configOpts == null) {
        configOpts = service.config;
      }
      self = service;
      if (configOpts.id !== self.config.id) {
        self.stopAutoRefresh();
        self.configure(configOpts);
        return self.load(true).then(self.startAutoRefresh);
      }
    };
    service.team = {};
    service.team.create = function(opts) {
      var data, q, self;
      self = service;
      data = {
        team: opts
      };
      q = $http.post(self.routes.createPath(), data).then(function(success) {
        self.data.push(success.data.team);
        return success.data.team;
      });
      return q;
    };
    service.team.update = function(teamId, opts) {
      var data, q, self;
      self = service;
      data = {
        team: opts
      };
      q = $http.put(self.routes.updatePath(teamId), data).then(function(success) {
        var team;
        team = _.find(self.data, function(t) {
          return t.id === teamId;
        });
        return angular.copy(success.data.team, team);
      });
      return q;
    };
    service.team.destroy = function(teamId) {
      var q, self;
      self = service;
      return q = $http["delete"](self.routes.destroyPath(teamId)).then(function(success) {
        var idx, team;
        team = _.find(self.data, function(t) {
          return t.id === teamId;
        });
        idx = self.data.indexOf(team);
        self.data.splice(idx, 1);
        return team;
      });
    };
    service.members = {};
    service.members.add = function(teamId, members) {
      var baseList, data, finalList, q, self;
      self = service;
      baseList = members;
      finalList = [];
      _.each(baseList, function(e) {
        if (angular.isObject(e)) {
          return finalList.push(e);
        } else {
          return finalList.push({
            id: e
          });
        }
      });
      data = {
        team: {
          users: finalList
        }
      };
      q = $http.put(self.routes.addMembersPath(teamId), data).then(function(success) {
        var team;
        team = _.find(self.data, function(t) {
          return t.id === teamId;
        });
        return angular.copy(success.data.team.users, team.users);
      });
      return q;
    };
    service.members.remove = function(teamId, members) {
      var baseList, data, finalList, q, self;
      self = service;
      baseList = members;
      finalList = [];
      _.each(baseList, function(e) {
        if (angular.isObject(e)) {
          return finalList.push(e);
        } else {
          return finalList.push({
            id: e
          });
        }
      });
      data = {
        team: {
          users: finalList
        }
      };
      q = $http.put(self.routes.removeMembersPath(teamId), data).then(function(success) {
        var team;
        team = _.find(self.data, function(t) {
          return t.id === teamId;
        });
        return angular.copy(success.data.team.users, team.users);
      });
      return q;
    };
    return service;
  }]);

}).call(this);



