(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeOrganizations', ["$state", "$cookies", "$log", "$q", "MnoeApiSvc", "MnoeCurrentUser", function($state, $cookies, $log, $q, MnoeApiSvc, MnoeCurrentUser) {
    var _self, organizationsApi;
    _self = this;
    organizationsApi = MnoeApiSvc.all('organizations');
    this.selectedId = null;
    this.getSelectedId = function() {
      return _self.selectedId;
    };
    this.selected = null;
    this.getSelected = function() {
      return _self.selected;
    };
    this.list = function() {
      return organizationsApi.getList().then(function(response) {
        _self.list = response;
        return response;
      });
    };
    this.get = function(id) {
      var organizationPromise;
      _self.selectedId = id;
      return organizationPromise = MnoeApiSvc.one('/organizations', id).get().then(function(response) {
        _self.selected = response.plain();
        $cookies.put('dhb_ref_id', _self.selectedId);
        return response;
      });
    };
    this.reload = function() {
      return _self.get(_self.selectedId);
    };
    this.create = function(organization) {
      return MnoeApiSvc.all('/organizations').post(organization).then(function(response) {
        _self.selected = response.plain();
        MnoeCurrentUser.user.organizations.push(_self.selected.organization);
        _self.selectedId = _self.selected.organization.id;
        return response;
      });
    };
    this.update = function(organization) {
      return organization.put();
    };
    this.updateCreditCard = function(creditCard) {
      return MnoeApiSvc.one('organizations', _self.selectedId).doPUT({
        "credit_card": creditCard
      }, '/update_billing').then(function(response) {
        return response.plain();
      });
    };
    this.getCurrentId = function(user, dhbRefId) {
      var deferred, organization, val;
      if (user == null) {
        user = null;
      }
      if (dhbRefId == null) {
        dhbRefId = null;
      }
      deferred = $q.defer();
      if (_self.selectedId) {
        $log.debug("MnoeOrganizations.getCurrentId: selectedId", _self.selectedId);
        deferred.resolve(_self.selectedId);
      } else if (dhbRefId) {
        _self.get(dhbRefId);
        $log.debug("MnoeOrganizations.getCurrentId: dhbRefId", _self.selectedId);
        deferred.resolve(dhbRefId);
      } else if ((val = $cookies.get('dhb_ref_id'))) {
        _self.get(val);
        $log.debug("MnoeOrganizations.getCurrentId: cookie", _self.selectedId);
        deferred.resolve(val);
      } else {
        organization = user.organizations[0];
        _self.get(organization.id);
        $log.debug("MnoeOrganizations.getCurrentId: first", _self.selectedId);
        deferred.resolve(organization.id);
      }
      return deferred.promise;
    };
    this.role = {};
    _self.role.isSuperAdmin = function() {
      return (_self.selected != null) && _self.selected.current_user.role === 'Super Admin';
    };
    _self.role.isAdmin = function() {
      return (_self.selected != null) && _self.selected.current_user.role === 'Admin';
    };
    _self.role.isPowerUser = function() {
      return (_self.selected != null) && _self.selected.current_user.role === 'Power User';
    };
    _self.role.isMember = function() {
      return (_self.selected != null) && _self.selected.current_user.role === 'Member';
    };
    _self.role.atLeastMember = function() {
      return true;
    };
    _self.role.atLeastPowerUser = function() {
      return _self.role.isPowerUser() || _self.role.isAdmin() || _self.role.isSuperAdmin();
    };
    _self.role.atLeastAdmin = function() {
      return _self.role.isAdmin() || _self.role.isSuperAdmin();
    };
    _self.role.atLeastSuperAdmin = function() {
      return _self.role.isSuperAdmin();
    };
    this.can = {};
    _self.can.read = {
      appInstance: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastMember();
      },
      billing: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastSuperAdmin();
      },
      member: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastMember();
      },
      organizationSettings: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastSuperAdmin();
      }
    };
    _self.can.create = {
      appInstance: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastAdmin();
      },
      billing: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastSuperAdmin();
      },
      member: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastAdmin() && (obj === null || obj.role !== 'Super Admin' || _self.role.isSuperAdmin());
      },
      organizationSettings: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastSuperAdmin();
      }
    };
    _self.can.update = {
      appInstance: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.appInstance(obj);
      },
      billing: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.billing(obj);
      },
      member: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.member(obj);
      },
      organizationSettings: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.organizationSettings(obj);
      }
    };
    _self.can.destroy = {
      appInstance: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.appInstance(obj);
      },
      billing: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.billing(obj);
      },
      member: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.member(obj);
      },
      organizationSettings: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.organizationSettings(obj);
      }
    };
    return this;
  }]);

}).call(this);



